import React, { useState, useEffect } from 'react';
import Button from '@mui/material/Button';
import { API_ENDPOINT } from '../config';
import axios from 'axios';
import ImageList from '@mui/material/ImageList';
import ImageListItem from '@mui/material/ImageListItem';
import Typography from '@mui/material/Typography';

function RenderImages(props) {
  const [imgUri, setImgUri] = useState([]);

  useEffect(() => {
    refreshImage()
  }, []);

  // TODO trigger by socket io event
  async function refreshImage(){
    const data = await axios.get(API_ENDPOINT + "get-all-images")
    setImgUri(data.data)
  }

  return(
    <>
    <Typography variant="h6" gutterBottom>
      Latets images
    </Typography>

    <Typography variant="caption" display="block" gutterBottom>
      scroll down
    </Typography>

    <ImageList sx={{ width: props.imageSize, height: props.imageSize }} cols={1} rowHeight={props.imageSize}>
      {imgUri.map((item) => (
        <>
        <ImageListItem key={item}>
          <img
            src={`${item}`}
            alt=""
            loading="lazy"
          />
        </ImageListItem>
        <Button
         size="small"
         variant="contained"
         onClick={() => alert("TO DO")}
        >
         process this image
        </Button>
        </>
      ))}
    </ImageList>
    </>
  )
}

export default RenderImages
