import React, { useState } from 'react';
import TextField from '@mui/material/TextField';
import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';
import Box from '@mui/material/Box';
import { API_ENDPOINT } from '../config';
import axios from 'axios';

function GenearateNewImage(props) {
  const [description, setDescription] = useState("");

  async function generateNew(){
    const delay = localStorage.getItem('delay')
    ? localStorage.getItem('delay')
    : 0

    const now = Date.now() / 1000
    if(delay > now)
      return alert(`Next request can be send after ${Number(delay - now).toFixed()} seconds`)

    if(description.length === 0)
      return alert("Please input description")

    try{
      const axiosConfig = {
        headers: {
          "Content-Type":'multipart/form-data',
          "Access-Control-Allow-Origin": "*"
        }
      }

      const body = { image_name:description }
      await axios.post(API_ENDPOINT + "trigger-bot", body, axiosConfig)

      alert("Request sent, you will be able to find your image in latest images in a few minutes")
    }catch(e){
      alert("Server error, please try latter")
    }

    localStorage.setItem('delay', (Date.now() / 1000) + 120) // delay 120 seconds
  }

  return(
    <>
    <TextField
      sx={{ width: props.imageSize }}
      id="outlined-textarea"
      label="Input description"
      onChange={(e) => setDescription(e.target.value)}
      multiline
    />

    <br/>
    <br/>
    <Box
      display="flex"
      justifyContent="center"
      alignItems="center"
    >
    <Stack spacing={2} direction="row">
      <Button variant="contained" onClick={() => generateNew()}>Imagine</Button>
    </Stack>
    </Box>
    </>
  )
}

export default GenearateNewImage
